<template>
  <div>
    <!--  -->
    <van-nav-bar title="" fixed placeholder left-arrow @click-left="$router.back()"></van-nav-bar>

    <!-- / -->

    <div class="container">
      <div class="article-content" ref="articleContent">
        <!--    -->
        <div class="articleContentTop" ref="articleContentTop">
          <h1 class="title">{{ title }}</h1>

          <van-skeleton title avatar :row="4" :loading="!article._id">
            <van-cell center class="user-info">
              <div slot="title" class="name">{{ article.author }}</div>
              <van-image slot="icon" class="avatar" round fit="cover" :src="article.avatar" />
              <div slot="label" class="pubdate">
                {{ article.time | formatterDate }}
              </div>
              <!-- <van-button class="follow-btn" type="default" icon="plus" round size="small"></van-button> -->
            </van-cell>
            <div class="markdown-body" v-html="article.content"></div>
          </van-skeleton>
        </div>

        <!--   fengexian  -->

        <van-divider :hairline="false" :style="{ borderColor: '#999', padding: '0 5px' }"></van-divider>


        <div class="commentListHead"></div>
        <!--  -->
        <div style="min-height: calc(100vh - 120px)">
          <van-list v-model="commentLoading" :finished="commentFinished" finished-text="" @load="getCommentList">
            <van-cell class="comment-item" v-for="(item, index) in commentList" :key="item._id">
              <van-image slot="icon" class="avatar" round fit="cover" :src="item.info.avatar" />
              <div slot="title">
                <div class="title-w">
                  <div class="name">{{ item.info.nickname }}</div>

                  <div class="like-w" @click="toggleCommentLike(item._id, index, item.is_like)">
                    <van-icon class="like-icon" :class="{ liked: item.is_like }" name="good-job" />
                    <span class="like-count">{{
                        item.like_count == 0 ? "" : item.like_count
                    }}</span>
                  </div>
                </div>
                <div class="content">{{ item.content }}</div>
                <div class="info">
                  <span class="pubdate">{{
                      item.create_time | formatterDate
                  }}</span>
                  <van-button class="reply-btn" @click="openReplyFiledPopup(item, index)" round size="mini">
                    {{ item.reply_num == 0 ? "" : item.reply_num }}

                    </van-button>
                </div>
              </div>
            </van-cell>
            <!-- / -->
          </van-list>
        </div>
      </div>
    </div>
    <a class="bshareDiv" href="http://www.bshare.cn/share"></a>
    <!-- / -->
    <div class="article-bottom">
      <van-button class="comment-btn" type="default" round size="small" @click="openFiledPopup"></van-button>

      <!--   -->
      <van-icon name="comment-o" color="#777" v-if="!article.comment || article.comment == 0" />
      <van-icon name="comment-o" color="#777" :badge="article.comment" v-else />

      <!--   -->
      <van-icon name="star" :color="article.is_fav ? '#f00' : '#777'" @click="toggleFav" />
      <!--  -->
      <van-icon name="good-job" :color="article.is_like ? '#f00' : '#777'" @click="toggleLike" />
      <van-icon name="share" @click="showShare = true" color="#777777"></van-icon>
    </div>
    <!--  -->

    <van-popup v-model="popupShow" position="bottom">
      <div class="post-comment">
        <van-field v-model="content" rows="4" autosize type="textarea" maxlength="1000"
          :placeholder="contentPlaceholder" show-word-limit ref="content" />
        <div style="width: 100%">
          <van-button size="large" type="primary" @click="addComment"></van-button>
        </div>
      </div>
    </van-popup>
    <!-- / -->

    <!--     -->

    <van-popup v-model="replyPopupShow" round position="bottom" z-index="99" style="height:100%">

      <replyComponent @close="replyPopupShow = false" @toggleCommentLike="toggleCommentLike"
        :active_reply_index="active_reply_index" :article_id="article_id"
        :replayItemInfo="commentList[active_reply_index]">

      </replyComponent>
      <div class="article-bottom">
        <van-button class="comment-btn" type="default" round size="small"
          @click="openReplyFiledPopup(commentList[active_reply_index], active_reply_index, true)"></van-button>


      </div>
    </van-popup>

    <!--   -->
    <van-share-sheet v-model="showShare" title="" :options="options" @select="onSelect" />


    <!--   -->
<van-popup v-model="qrcodePopup" round position="center" z-index="99" >
   <van-image :src="qrcodeurl" >

   </van-image>
   <button><a :href="qrcodeurl" download></a></button>
</van-popup>
  </div>
</template>

<script>
import "@/common/github-markdown.css";

import { Popup, Field, Skeleton, Divider, ShareSheet } from "vant";

import { getArticleDetail, addComment } from "@/api/api";

import { mapState } from "vuex";

import commentListMixin from "./commentList";

import methods from "./articleMethods";

import replyComponent from './replyComponent.vue'

import QRCode from 'qrcode'
import html2canvas from 'html2canvas'

export default {
  data() {
    return {
      popupShow: false,
      content: "",
      article_id: "", //  id
      title: "", //  
      article: {}, //  
      comment_type: 0, //comment_type0 1
      articleContentTop: 0, //  
      contentPlaceholder: "",
      reply_comment_id: "", //   ID
      active_reply_index: 0,
      replyPopupShow: false,
      showShare: false,
      options: [
        { name: '', icon: 'wechat' },
        { name: '', icon: 'weibo' },
        { name: 'QQ', icon: 'qq' },
        { name: '', icon: 'link' },
        { name: '', icon: 'poster' },
        { name: '', icon: 'qrcode' },
      ],
      qrcodePopup:false,//  
      qrcodeurl:''
    };
  },
  mixins: [commentListMixin],
  components: {
    [Popup.name]: Popup,
    [Field.name]: Field,
    [Skeleton.name]: Skeleton,
    [Divider.name]: Divider,
    [replyComponent.name]: replyComponent,
    [ShareSheet.name]: ShareSheet
  },
  computed: {
    ...mapState(["uid", "userInfo"]),
  },
  created() {
    let { id, title } = this.$route.query;
    this.article_id = id;
    this.title = title;
    this.getArticleDetail();
  },
  methods: {

    copy(params) {

      let input = document.createElement('input')
      input.value = params;
      // input.type = 'hidden'
      document.body.appendChild(input);
      input.select();
      document.execCommand("Copy");   // 
      document.body.removeChild(input)
    },
    shareTSina() {

      let img = this.article.imageSrc[0];
      let title = this.article.title;

      console.log(img)

      let url = window.location.href;
      window.open("http://service.weibo.com/share/share.php?pic=" + encodeURIComponent(img) + "&title=" +
        title + "&url=" + url);
    },
    shareQQ() {

      let img = this.article.imageSrc[0];
      let title = this.article.title;

      console.log(img)

      let url = window.location.href;
      window.open("http://connect.qq.com/widget/shareqq/index.html?pic=" + encodeURIComponent(img) + "&title=" +
        title + "&url=" + url);
    },
    qrcode(){
 let url = window.location.href;
      QRCode.toDataURL(url)
      .then(url => {
        console.log(url)

       this.qrcodeurl = url;
       this.qrcodePopup = true

      })
      .catch(err => {
        console.error(err)
      })

    },

    onSelect({ name }) {
      console.log(name)


      switch (name) {
        case '':
          console.log(window.location.href)
          this.$toast(',')
          this.copy(window.location.href)
          break;

        case '':
          this.shareTSina();
          break

        case 'QQ':
          this.shareQQ();
          break
        case '':
        case '':
          this.qrcode();
          break

          case "":
            let articleContentTop =  this.$refs.articleContentTop
                if(articleContentTop){
                  html2canvas(articleContentTop,{
                    allowTaint:true
                  }).then(canvas=>{
                    console.log(canvas);
var cxt=canvas.getContext('2d');
                    let img = new Image();
                    img.src = require('@/views/article/1.png');
                    img.onload = function(){
                   cxt.drawImage(img,10,10);  

                    let res = canvas.toDataURL(); //  

console.log(res) 

                    }
 
 



                  }).catch(err=>{

                  })
                }
            

      }




      this.showShare = false;
    },
    ...methods,
    //  
    openFiledPopup() {
      if (!this.checkLogin()) return;
      this.contentPlaceholder = "";
      this.comment_type = 0;
      this.popupShow = true;
      this.$nextTick(function () {
        console.log(this.$refs.content);
        this.$refs.content.focus();
      });
    },
    openReplyFiledPopup(item, index, is_child) {
      if (!this.checkLogin()) return;
      let {
        reply_num,
        info: { nickname },
        _id: reply_comment_id,
      } = item;
      this.active_reply_index = index;


      if (reply_num == 0 || is_child) {
        //  
        this.popupShow = true;
        this.comment_type = 1;
        this.contentPlaceholder = ":" + nickname;
        this.reply_comment_id = reply_comment_id;

        this.$nextTick(function () {
          this.$refs.content.focus();
        });
      } else {
        //  


        this.replyPopupShow = true

      }
    },

    //  
    getArticleDetail() {
      let { article_id, uid: user_id } = this;
      getArticleDetail({
        user_id,
        article_id,
      }).then((res) => {
        console.log(res);
        if (res.code == 0) {
          this.article = res.data;
          //  console.log(this.$refs.articleContentTop.offsetHeight)   //  51  51 dom

          //  ->dom  -> dom  
          this.$nextTick(function () {
            //  console.log(this.$refs.articleContentTop.offsetHeight)  ;
            this.articleContentTop = this.$refs.articleContentTop.offsetHeight;
          });
        } else {
          this.$toast(res.msg);
        }
      });
    },
    //
    addComment() {
      let {
        uid: user_id,
        article_id,
        content,
        comment_type,
        reply_comment_id,
      } = this;

      content = content.trim();

      if (content.length == 0) {
        this.$toast("");
        return;
      }

      addComment({
        user_id,
        article_id,
        content,
        comment_type,
        reply_comment_id,
      }).then((res) => {
        this.$toast(res.msg);
        if (res.code == 0) {
          this.popupShow = false;

          this.article.comment++;
          this.content = "";

          if (comment_type == 0) {
            let { avatar, nickname, username } = this.userInfo;

            let commentData = {
              article_id,
              comment_type: 0,
              content,
              create_time: new Date().getTime(),
              info: {
                avatar,
                nickname,
                username,
              },
              is_like: false,
              like_count: 0,
              reply_comment_id: "",
              reply_num: 0,
              user_id,
              _id: res.data.id,
            };
            this.skip++;
            this.commentList.unshift(commentData);
            //    
            this.$refs.articleContent.scrollTo(0, this.articleContentTop);
          } else if (comment_type == 1) {

            if (this.replyPopupShow) {

              //      
              console.log(11)
              this.$Bus.$emit('listRefresh')

            } else {
              this.commentList[this.active_reply_index].reply_num++;

              let index = this.active_reply_index
              let item = this.commentList[index];

              this.openReplyFiledPopup(item, index)

            }


          }



        }
      });
    },
  },
};
</script>

<style scoped lang="scss">
.article-content {
  overflow-y: auto;
  padding-bottom: 44px;
  height: calc(100vh - 92px);

  .title {
    font-size: 20px;
    color: #3a3a3a;
    padding: 24px 20px 18px;
    background-color: #fff;
    margin: 0;
  }
}

.user-info {
  .avatar {
    width: 35px;
    height: 35px;
    margin-right: 8px;
  }

  .name {
    font-size: 12px;
    color: #333333;
  }

  .pubdate {
    font-size: 11px;
    color: #b4b4b4;
  }

  .follow-btn {
    width: 85px;
    height: 29px;
  }
}

ul {
  list-style: unset;
}

.markdown-body {
  padding: 14px;
  background-color: #fff;
}

.article-bottom {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-sizing: border-box;
  height: 44px;
  border-top: 1px solid #d8d8d8;
  background-color: #fff;

  .comment-btn {
    width: 141px;
    height: 23px;
    border: 1px solid #eeeeee;
    font-size: 15px;
    line-height: 23px;
    color: #a7a7a7;
  }

  .van-icon {
    font-size: 20px;

    .van-info {
      font-size: 11px;
      background-color: #e22829;
    }
  }
}

.comment-item {
  .avatar {
    width: 36px;
    height: 36px;
    margin-right: 13px;
  }

  .name {
    font-size: 16px;
    color: #555;
  }

  .content {
    font-size: 16px;
    color: #222222;
    margin: 10px 0;
  }

  .info {
    display: flex;
    height: 36px;
    align-items: center;
  }

  .pubdate {
    font-size: 12px;
    margin-right: 12px;
  }

  .title-w {
    display: flex;
    justify-content: space-between;
    height: 36px;
    align-items: center;
  }

  .like-w {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .like-icon.liked {
    color: #ef092c;
  }
}

.article-bottom {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-sizing: border-box;
  height: 44px;
  border-top: 1px solid #d8d8d8;
  background-color: #fff;

  .comment-btn {
    width: 200px;
    height: 30px;
    border: 1px solid #eeeeee;
    font-size: 15px;
    line-height: 23px;
    color: #a7a7a7;
  }
}

.post-comment {
  padding: 14px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.commentListHead {
  padding: 10px;
  font-weight: bold;
  font-size: 20px;
}
</style>
